.style__switcher{
    position: fixed;
    left: 0;
    top: 10%;
    background-color: var(--text-white);
    width: 220px;
    padding: 15px;
    box-shadow: 0 0 4px hsl(0, 0, 80%);
    z-index: 10;
    transform: translateX(-100%);
    transition: all 0.3s ease;
}

.show-switcher{
    transform: translateX(0);
}


.style__switcher,
.style__switcher-toggler,
.theme__toggler{
    border-radius: 0 5px 5px 0;
}


.style__switcher-item{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    column-gap: 4px;
    row-gap: 8px;
}

.style__switcher-title,
.style__switcher-toggler,
.theme__toggler,
.style__switcher-close{
    
    color:hsl(0, 3%, 23%);

}

.style__switcher-title{
    font-size: var(--small-font-size);
    font-weight: 600;
    border-bottom: 1px solid rgb(170, 159, 159);
    padding-bottom: 5px;
    margin-bottom: 15px;
}

.style__switcher-toggler,
.theme__toggler{
    position: absolute;
    left: 100%;
    background-color: var(--text-white);
    height: 50px;
    width: 50px;
    font-size: var(--large-font-size);
    cursor: pointer;
    display: grid;
    place-items: center;
    box-shadow: 0 0 2px hsl(0, 0%, 80%);
    transition: all 0.3s ease;
}


.show-switcher .style__switcher-toggler,
.show-switcher .theme__toggler{
    left: -100%;
}

.style__switcher-toggle{
    top: 10%;
}

.theme__toggler{
    bottom: 10%;
}

.style__switcher-close{
    position: absolute;
    font-size: var(--h2-font-size);
    cursor: pointer;
    top: 2%;
    right: 15px;
}



@media(max-width: 668px){
    .style__switcher-toggler,
.theme__toggler{
    height: 30px;
    width: 30px;
}
}

